*, *::after, *::before{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}


body{
  margin-top: 90vh;
 background: rgb(49, 48, 48);
 padding-bottom: 90vh;
}


.box{
  height: 50px;
  width: 50px;
  display:grid;
  justify-items: center;
  align-items: center;
  font-size: 20px;
  font-weight: bold;
  color: #fff;
  margin-bottom: 100px;
}


.a {
  background-color: purple;
}

.b {
  background-color: green;
}

.c {
  background-color:  orange;
}



